import { Component,ViewChild, OnInit } from '@angular/core';
import {Wizard} from "clarity-angular";
import {Router} from '@angular/router';
import {ClaveprofesorService} from '../claveprofesor.service';
import {Estatus} from '../estatus';
import {Materia} from '../principal/Materia'


@Component({
  selector: 'app-registroprofesor',
  templateUrl: './registroprofesor.component.html',
  styleUrls: ['./registroprofesor.component.css'],
  providers:[ClaveprofesorService]
})
export class RegistroprofesorComponent implements OnInit {

  @ViewChild("wizardlg") wizardLarge: Wizard;
  
    
    @ViewChild("myForm") formData: any;
    
        loadingFlag: boolean = false;
        errorFlag: boolean = false;
  
        modelo={
          nombre:'',
          paterno:'',
          materno:'',
          sistema:''
        }

     //Los siguientes atributos y metodos son  necesarios para las materias
     materias:Materia[]=[];
     
       toAdd: Materia[] = [];
       toDelete: Materia[] = [];
       toEdit: Materia;
       _selected: Materia[] = [];
       
     
     
       get selected() {
         return this._selected;
     }
     
     set selected(selection: Materia[]) {
         this._selected = selection;
         this.cleanUp();
     }
     cleanUp(): void {
       this.toAdd = [];
       this.toDelete = [];
       this.toEdit = null;
     }
     
     
     
     onDelete(materia: Materia) {
       this.cleanUp();
       if (materia) {
           this.toDelete = [materia];
       } else {
           this.toDelete = this.selected.slice();
       }
     }
     
     onEdit(materia: Materia) {
       this.cleanUp();
       if (materia) {
           this.toEdit = materia;
       } else {
           this.toEdit = this.selected[0];
           console.log(this.selected[0]);
       }
     }
     
     onAdd() {
       this.cleanUp();
       this.toAdd = this.selected.slice();
       console.log(this.toAdd);
     }
    
        // have to define doCancel because page will prevent doCancel from working
        // if the page had a previous button, you would need to call 
        // this.wizard.previous() manually as well...
        doCancel(): void {
            this.wizardLarge.close();
        }
    
        onCommit(): void {
            let value: any = this.formData.value;
            this.loadingFlag = true;
            this.errorFlag = false;
              value.miClave
  
              this.obtenerClaveProfesor();
    
            setTimeout(() => {
                if (this.estatus.success) {
                    this.wizardLarge.forceNext();
                    console.log('Encontrado!!');
                } else {
                    this.errorFlag = true;
                }
                this.loadingFlag = false;
            }, 1000);
        }
      
    
    lgOpen: boolean = true;
  
    perfil:string='Alumno';
    miClave:string;
    noEsProfesor:boolean=true;
    estatus:Estatus;
    oculta:boolean=true;
  
    constructor(private router:Router, private servicioClave:ClaveprofesorService) {
  
    this.oculta=true;

//materias para el perfil
this.materias.push(new Materia('Matemáticas','Matemáticas 1'));
this.materias.push(new Materia('Matemáticas','Matemáticas 2'));
this.materias.push(new Materia('Matemáticas','Matemáticas 3'));
this.materias.push(new Materia('Matemáticas','Matemáticas 4'));
this.materias.push(new Materia('Matemáticas','Matemáticas 5'));
this.materias.push(new Materia('Matemáticas','Matemáticas 6'));
this.materias.push(new Materia('Comunicación','Lenguaje y comunicación 1'));
this.materias.push(new Materia('Comunicación','Lenguaje y comunicación 2'));
this.materias.push(new Materia('Comunicación','Lenguaje y literatura 1'));
this.materias.push(new Materia('Comunicación','Lenguaje y literatura 2'));
this.materias.push(new Materia('Comunicación','Taller de análisis de los textos 1'));
this.materias.push(new Materia('Comunicación','Taller de análisis de los textos 2'));
this.materias.push(new Materia('Informática','Informática 1'));
this.materias.push(new Materia('Informática','Informática 2'));
this.materias.push(new Materia('Informática','Informática 3'));
this.materias.push(new Materia('Informática','Informática 4'));
this.materias.push(new Materia('Desarrollo Humano','Orientación educativa'));
this.materias.push(new Materia('Desarrollo Humano','Orientación vocacional'));
this.materias.push(new Materia('Desarrollo Humano','Actividades deportivas'));


     }
  
    ngOnInit() {
      this.estatus=new Estatus(false);
  
    }
  
  
  obtenerClaveProfesor(){
    this.getClaveProfesor();
    console.log('Hola'+this.estatus.success);
  
  }
  navegarInicio(){
    this.router.navigate(["/inicio"],{skipLocationChange:true});
  }
  
  verPerfil(){
    console.log('hola');
    if(this.perfil==='Profesor'){
      console.log('Eres profesor');
      this.noEsProfesor=false;
  
    }else{
      console.log('No eres profesor');
      this.noEsProfesor=true;
      }
  
   
    }
  
  
   getClaveProfesor(){
     this.servicioClave.getClaveProfesor(this.miClave)
     .subscribe(estatus=>this.estatus=estatus);
       
     }
  
     hacerAlgo(){
       console.log('asasasas');
      // this.=this.estatus.success;
     }
     estaInvalidada():boolean{
  this.oculta=this.estatus.success;
  return !this.estatus.success;
  
     }
  
   
    
  }
  


