import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import{InicioComponent} from './inicio/inicio.component';
import{ExamenesComponent} from './examenes/examenes.component';
import {PrincipalComponent} from './principal/principal.component'
import {RegistroalumnoComponent} from './registroalumno/registroalumno.component'
import{RegistroprofesorComponent} from './registroprofesor/registroprofesor.component'

const routes: Routes = [
  { path:'', redirectTo:'inicio',pathMatch:'full',},
  {path:'inicio',component:InicioComponent},
  {path:'registro-alumno', component:RegistroalumnoComponent},
  {path:'registro-profesor', component:RegistroprofesorComponent},
  {path:'examenes', component:ExamenesComponent},
  {path:'principal',component:PrincipalComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }



